.main {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: #282c34;
    box-sizing: border-box;
    overflow: auto;
}

.wrapper {
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 300px;
}

h1, h3, p, span.dataText {
    margin: 0;
    color: azure;
}

.header {
    margin: 16px 0;
}

.dataContainer {
    display: flex;
    flex-direction: column;
    padding: 6px 16px;
    font-family: "Roboto","Helvetica","Arial",sans-serif;
    font-size: 0.875rem;
    background-color: #1976d2;
    border: 1px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

.dataText {
    text-align: left;
    font-size: 24px;
}

span.dataText {
    font-weight: lighter;
}

.desc p {
    color: #282c34;
}
p.descItem{
    margin-bottom: 16px;
}

.releaseContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 2px solid white;
    margin: 16px 0;
    height: 300px;
}

.releaseIngress {
    font-size: 24px;
    margin-bottom: 24px;
}

.releaseDate {
    font-size: 30px;
    font-weight: bold;
}